* {
  box-sizing: border-box;
}

body, h1, h2, h3, p, ul, li, article, fieldset, legend, input, textarea {
  font: inherit;
  border: 0;
  margin: 0;
  padding: 0;
}

.animated--section {
  animation: 2s forwards slideDown;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-25%)translateX(25%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fade-in-out {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media (min-width: 640px) {
  .animated--team-bg {
    animation: 4s ease-in-out fade-in-out;
  }
}

.animated--discount {
  opacity: 0;
  animation: 2.5s .5s forwards rotate;
}

@keyframes rotate {
  0% {
    opacity: 0;
    transform: scale(1)rotate(0);
  }

  50% {
    opacity: 1;
    transform: scale(2.5)rotate(370deg);
  }

  100% {
    opacity: 1;
    transform: scale(1)rotate(360deg);
  }
}

.animated--stamp {
  animation: 15s linear infinite rotateStamp;
}

@keyframes rotateStamp {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

