.button {
  display: flex;
  position: relative;
  align-items: center;
  cursor: pointer;
  // width: 260px;
  height: 48px;
  // padding: 14px 72px;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 150%;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s;

  &:hover {
    transform: scale(1.2);
  }

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 27px;
    height: 8px;
    right: 72px;
    background-image: url(../images/icons/icon-arrow.svg);
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;

    // &:first-of-type {
